.MainContentList {
    width: 100%;
    max-width: 1200px;
    margin: 40px auto;
    padding: 0 20px;
    box-sizing: border-box;
}

.columns {
    display: grid !important;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr)) !important;
    grid-gap: 30px !important;
    margin: 0 auto;
}

ul {
    max-width: 1050px;
    margin: 0 auto 30px;
}

.box {
    padding: 15px;
}